#nav-icon4 {
  width: 21px;
  height: 16px;
  cursor: pointer;
  transition: all .5s ease-in-out;
}

#nav-icon4 span {
  height: 3px;
  width: 100%;
  opacity: 1;
  background: #fff;
  border-radius: 3px;
  transition: all .25s ease-in-out;
  display: block;
  position: absolute;
  left: 0;
  transform: rotate(0deg);
}

#nav-icon4 span:nth-child(1) {
  transform-origin: 0;
  top: 0;
}

#nav-icon4 span:nth-child(2) {
  transform-origin: 0;
  top: 6px;
}

#nav-icon4 span:nth-child(3) {
  transform-origin: 0;
  top: 12px;
}

#nav-icon4.open span:nth-child(1) {
  top: -2px;
  left: 3px;
  transform: rotate(45deg);
}

#nav-icon4.open span:nth-child(2) {
  width: 0%;
  opacity: 0;
}

#nav-icon4.open span:nth-child(3) {
  top: 13px;
  left: 3px;
  transform: rotate(-45deg);
